#main {
    box-shadow: 0 0 3px var(--white200);
    background-color: var(--mainBg);
    position: relative;
    min-height: 100vh;
    margin-right: 400px;
    margin-left: 260px;
}

.loading {
    display: flex;
    align-items: center;
    justify-content: center;
    min-height: calc(100vh);
    background-color: var(--mainBg);
}

.loading2 {
    display: flex;
    align-items: center;
    justify-content: center;
    height: 300px;
}

.error {
    display: flex;
    align-items: center;
    justify-content: center;
    flex-direction: column;
    min-height: calc(100vh - 97.5px);

    svg {
        font-size: 3rem;
        color: var(--pointColor);

        span {
            display: block;
        }
    }
}

.Toastify__toast-body {
    font-family: var(--fontJ) !important;
}

