.overlay {
    position: fixed;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    background-color: rgba(0, 0, 0, 0.75);
}

/* src/App.css 또는 적절한 CSS 파일 */
.chart__wrap {
    max-height: 300px;
    .chart {
        position: relative;
        height: 300px;
        div {
            height: 300px;
            canvas {
                height: 300px;
            }
        }
    }
}
